import React from 'react';

const Pricing = ({ data, variant }) => {
  return (
    <div className='md:flex mt-11 border-[#eeeeee] border border-t-0'>
      {data.map((item, i) => (
        <div key={i} className='flex flex-col text-gray w-full bg-white first:border-0 border-l-[1px] border-[#eeeeee]'>
          {variant !== 'alternative' ? (
            <>
              <div className='flex items-center justify-center bg-skyblue min-h-[90px]'>
                <p className='p-2 text-xl font-light tracking-widest text-center text-white'>{item.title}</p>
              </div>
              <div className='flex justify-center flex-grow-[1]'>
                <p className='p-5 text-sm antialiased text-center'>{item.subTitle}</p>
              </div>
              <div className='p-3 text-sm antialiased bg-lightgray'>
                <ul className='flex-col items-center justify-center text-center md:flex'>
                  {item.details.map((detail, i) => (
                    <li key={i} className='my-2 md:min-h-[22px]'>
                      <p>
                        {detail.description != null && <span>{detail.description} minutes -</span>}
                        {detail.price != null && <span> £{detail.price}</span>}
                      </p>
                    </li>
                  ))}
                </ul>
              </div>
            </>
          ) : (
            <>
              <div className='flex items-center justify-center bg-skyblue min-h-[90px]'>
                <p className='p-2 text-xl font-light tracking-widest text-center text-white'>{item.title}</p>
              </div>
              <div className='p-3 text-sm antialiased'>
                {item.details.map((detail, i) => (
                  <div key={i} className='flex text-center my-2 md:min-h-[22px]'>
                    {detail.description != null && <p className='w-1/2'>{detail.description}</p>}
                    {detail.price != null && <div className='w-1/2'>£{detail.price}</div>}
                  </div>
                ))}
              </div>
            </>
          )}
        </div>
      ))}
    </div>
  );
};

export default Pricing;
